<template>
  <input
    :class="valid ? `input-text placeholder-red-500 placeholder-opacity-50 ${sizeClass}` 
                  : `input-text ${sizeClass}`"
    :placeholder="valid || name"
    :value="value"
    @input="$emit('getval', $event.target.value)"
  />
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      required: true
    },
    valid: String,
    sizeClass: String,
    name: String
  }
}
</script>

