<template>
  <section class="sfile-main md:h-screen min-h-screen w-full md:w-1/2" id="savefile">
    <!-- Header -->
    <h1 class="sfile-title text-2xl md:text-3xl">Save File</h1>

    <!-- Input File Upload -->
    <div class="w-full px-5 flex justify-center items-center mb-2">

      <div class="w-full lg:w-1/2 flex flex-col">
      <!-- Input Filename -->
        <Input
          autocomplete="off"
          id="filename"
          name="Filename.."
          sizeClass="w-full"
          :value="filename" 
          @getval="filenameChange"
          maxlength="30"
        />

        <!-- Upload Button -->
        <div class="upload-button bg-white hover:bg-gray-200 text-sm md:text-base"
            @click="uploadFile">
          <ProgressUpload :progress="progressUpload"/>
          <button class="flex items-center focus:outline-none" >
            <SVGIcon name="upload" size="w-5 md:w-6 h-5 md:h-6" />
            <h1>Upload</h1>
          </button>
        </div>
      </div>
      
      <!-- Open Folder Button -->
      <label for="file" class="relative open-folder-button bg-white hover:bg-gray-200 h-16 md:h-full" v-cloak 
             ref="uploadFile"
             @drop.prevent="e => changeFile(e, 'drop')"
             @dragover.prevent
             @dragenter.prevent="beforeDropFile('enter')"
             @dragleave.prevent="beforeDropFile('leave')"
      > 
        <div class="m-1">
          <img v-if="fileUpload" @error="setDefaultImage" 
               class="w-16 h-16 object-cover object-center"
               :class="isDefaultImage ? 'invert' : ''"
               :src="fileUpload">
          <div v-else class="flex flex-col justify-center items-center">
            <SVGIcon name="folder" size="w-6 h-6" />
            <p class="text-xs hidden md:block">open / drop</p>
          </div>

        </div>
        <input type="file" ref="file" id="file"  @change="changeFile" class="hidden">
      </label>

    </div>
    <!-- end Input File upload -->

    <!-- Notif Validation -->
    <span v-if="validationUpload" class="notif" >
      <p class="mr-2 text-sm">{{ validationUpload }}</p>
      <button class="absloute right-0 top-0 close-notif bg" @click="validationUpload = null" >x</button>
    </span>

    <!-- List File -->
    <div class="file-list md:overflow-auto">
      <ul>
        <li v-if="!lists.length" class="text-center font-semibold text-gray-200 mt-5">Tidak ada file yang tersimpan.</li>
        <List
          mode="dark"
          v-for="list of lists" :key="list.id"
          :data="list"
          :showOption="showOption"
          @toggle="(id) => toggle(id)"
        >

        <!-- Content (title and date) -->
        <template v-slot:content>
          <div class="flex flex-row items-center w-full text-white">
            <img :src="list.content" 
               @error="setReferenceImage" 
               class="w-24 h-24 object-cover mr-3">
            <div>
              <h3 class="font-bold break-all">{{ list.title }}</h3>
              <p class="text-gray-400 text-xs">{{ new Date().formateDate(list.date) }}</p>
            </div>
          </div>
        </template>

        <!-- Action (download) -->
        <template v-slot:action>
          <a :href="list.content" 
             :download="list.title"
             @click.stop
             class="bg-white hover:bg-gray-200 text-gray-700 rounded px-2 py-1 mt-2 lg:mt-0"
             target="_blank">
               {{ actionCompleteId == list.id ? 'downloaded' : 'download' }}
          </a>
        </template>

        <!-- Option (delete) -->
        <template v-slot:option>
          <ButtonCircle mode="danger" @klik="deleteFile(list)">
            <SVGIcon name="trash" size="w-5 h-5" />
          </ButtonCircle>
        </template>
        </List>
      </ul>
    </div>
    <!-- List File End -->
    
  </section>
</template>

<script>

import ButtonCircle from '@/components/ButtonCircle';
import SVGIcon from '@/components/SVGIcon';
import List from '@/components/List';
import Input from '@/components/Input';
import ProgressUpload from '@/components/ProgressUpload';

import defaultFile from '@/assets/images/default-file.png';

export default {
  components: {
    ButtonCircle,
    SVGIcon,
    List,
    Input,
    ProgressUpload
  },
  data(){
    return {
      showOption: null,
      actionCompleteId: null,
      lists: [],
      filename: '',
      fileUpload: '',
      isDefaultImage: false,
      validationUpload : null,
    }
  },
  computed: {
    progressUpload() { 
      return this.$store.state.file.progressUpload
    },
    userRef(){
      return this.$store.getters['user/userRef']
    },
    dragSupport(){
      let div = document.createElement('div');
       return (('draggable' in div) || ('ondragstart' in div && 'ondrop' in div)) && 'FormData' in window && 'FileReader' in window;
    }
  },
  mounted(){
    this.userRef.collection('file')
      .onSnapshot(snaps => {
        this.lists = []
        snaps.forEach(snap => {
          let data = snap.data()
          data['id'] = snap.id
          this.lists.push(data)
        })
      })
  },
  methods: {
    beforeDropFile(action){
      if(action === 'enter'){
        this.$refs.uploadFile.classList.add('bg-green-500')
        console.log('enter')
      } else if(action === 'leave') {
        console.log('leave')
        this.$refs.uploadFile.classList.remove('bg-green-500')
      } else {
        return 
      }
    },
    toggle(id){
      if(this.showOption == id){
        this.showOption = null
      } else this.showOption = id
    },
    filenameChange(val){
      if(this.filename.length > 30) return
      this.filename = val
    },
    setReferenceImage(e){
      e.target.src = defaultFile
    },
    extenstionFile(file){
      return file.split('.').pop()
    },
    changeFile(e, mode){
      this.isDefaultImage = false

      let file
      if(mode === 'drop'){
        this.dragSupport 
          ? file = e.dataTransfer.files[0]
          : this.validationUpload = 'Browser anda tidak support drag & drop file'
      } else file = e.target.files[0]

      if(file.size > 3145728) {
        this.validationUpload = 'Ukuran file tidak boleh lebih besar dari 3mb'
        e.target.value = ''
        return 
      }
      const reader = new FileReader()
      reader.addEventListener('load', () => {
        this.fileUpload = reader.result
      }, false)
      if (file) {
        reader.readAsDataURL(file);
      }
    },
    setDefaultImage(){
      this.fileUpload = defaultFile
      this.isDefaultImage = true
    },
    uploadFile(){
      if(this.$refs.file.files[0]){
        const file = this.$refs.file.files[0]

        let filename;
        const ext = '.'+this.extenstionFile(file.name)
        if(this.filename){
          filename = this.filename + ext
        } else {
          let name = file.name.split('.')[0]
          filename = file.name.length > 30 ? name.slice(0, 30) + ext : name + ext
        }

        this.$store.dispatch('file/Upload', {filename, file}, {root:true})
          .then(() => {
            this.filename = ''
            this.fileUpload = ''
          })

      } else {
        alert('Pilih terlebih dahulu file yang akan diupload.')
      }
    },
    deleteFile(data){
      if(confirm('yakin ?')){
        this.$store.dispatch('file/Delete', data, {root:true})
      }
    }
    
  }
}
</script>

<style lang="postcss" scoped>
  .sfile-main{
    @apply bg-gradient-to-br from-gray-800 to-blue-900 flex flex-col items-center
  }
  .sfile-title{
    @apply my-2 font-bold text-white
  }
  .file-list{
    @apply py-2 px-5 text-base tracking-wide text-left w-full
  }
  .upload-button {
    @apply mt-1 w-full flex justify-center items-center cursor-pointer rounded py-1 relative
  }
  .open-folder-button {
    @apply ml-1 w-24 rounded cursor-pointer flex justify-center items-center 
  }
  .invert{
    filter: invert(1);
  }
  .notif {
    @apply bg-white text-red-600 px-2 py-1 text-sm rounded leading-relaxed flex items-center
  }
  .close-notif{
    @apply w-5 h-5 bg-red-600 rounded-full p-2 text-white flex justify-center items-center text-sm outline-none
  }
</style>