<template>
  <button
    :class="`focus:outline-none focus:border-blue-500 focus:border-3 ${shadow || ''} ${full ? 'rounded-full' : 'rounded'}
  ${color == 'white' ? 'bg-white hover:bg-gray-200 text-gray-700'
        : `bg-${color}-500 hover:bg-${color}-600 text-white`}
  ${lg ? 'px-6 py-2 text-xl' 
       : md ? 'px-3 py-1 text-md'
       : sm ? 'px-2 py-1 text-sm'
       : 'px-3 py-1' } ${className || ''}`"
  >
    <slot></slot>
  </button>
</template>

<script>
export default {
  props: {
    color: String,
    shadow: String,
    lg: Boolean,
    md: Boolean,
    sm: Boolean,
    className: String,
  },
};
</script>

