<template>
  <section class="stext-main md:h-screen min-h-screen w-full md:w-1/2" id="savetext">

    <!-- Title -->
      <h1 class="stext-title text-2xl py-1 md:py-0 md:text-3xl">Save Text</h1>

    <!-- Menu Toggle -->
    <div class="absolute top-0 left-0 m-4 cursor-pointer p-1 hover:bg-gray-200 rounded-full" @click="$store.commit('TOGGLE_MENU')">
      <svg v-if="!$store.state.menu"  class="w-6 h-6 text-gray-700" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16"></path></svg>
      <SVGIcon v-else name="x" size="w-6 h-6" color="text-gray-700" />
    </div>

    <!-- Header -->
    <div class="w-full px-5 mb-3">

      <!-- Form Input -->
      <form @submit.prevent="saveText" class="flex flex-col lg:flex-row lg:items-center w-full">
        <div class="flex flex-col lg:flex-row w-full">
          <Input
            class="mb-2 lg:mb-0 mr-2"
            name="Title"
            sizeClass="w-full lg:w-3/12"
            :valid="validation" 
            :value="input.title" 
            @getval="(val) => input.title = val"/>
          <Input
            class="mb-2 lg:mb-0 mr-2"
            name="paste your link or something text"
            sizeClass="w-full lg:w-9/12"
            :valid="validation" 
            :value="input.text" 
            @getval="(val) => input.text = val"/>
        </div>
        <Button type="submit" color="blue" md className="text-base" >{{ isUpdate ? 'Update' : 'Save' }}</Button>
      </form>

    </div>
    <!-- Header End -->

    <!-- List Text -->
    <ul class="text-list md:overflow-auto">

      <!-- jika list kosong -->
      <li v-if="!lists.length" class="text-center font-semibold text-gray-700 mt-5">Tidak ada text yang tersimpan.</li>

      <!-- list views -->
      <List
        mode="light"
        v-for="list of lists" :key="list.id"
        :data="list"
        :showOption="showOption"
        :isUpdate="isUpdate"
        @toggle="(id) => toggle(id)"
      >

      <!-- Content (title and date) -->
        <template v-slot:content>
          <div class="flex flex-col mb-2 lg:mb-0 w-full overflow-hidden 'text-white">
            <div class="mb-1">
              <h3 class="font-bold">{{ list.title }}</h3>
              <p class="text-gray-500 text-xs">{{ new Date().formateDate(list.date) }}</p>
            </div>
            <p class="overflow-hidden" :class="{'text-blue-500': isUrl(list.content)}">{{ list.content }}</p>
          </div>
        </template>

        <!-- action (copy and goto) -->
        <template v-slot:action>
          <Button 
            color="green" 
            @click.stop.native="copyText(list)"
            sm>
            {{ actionCompleteId == list.id ? 'copied' : 'copy' }}
          </Button>
          <a v-if="isUrl(list.content)"
            @click.stop
            class="bg-blue-500 hover:bg-blue-600 rounded p-1 text-white ml-2"
            :href="list.content"
            target="_blank" title="go to link"
          >
            <SVGIcon name="goto" size="w-5 h-5" />
          </a>
        </template>

        <!-- options (edit, delete) -->
        <template v-slot:option>
          <ButtonCircle mode="warning" @klik="editText(list)">
            <SVGIcon name="pancil" size="w-5 h-5" />
          </ButtonCircle>
          <ButtonCircle mode="danger" @klik="deleteText(list.id)">
            <SVGIcon name="trash" size="w-5 h-5" />
          </ButtonCircle>
        </template>

      </List>
    </ul>
    <!-- End List Text -->

  </section>
</template>

<script>
import Button from "@/components/Button"
import ButtonCircle from '@/components/ButtonCircle';
import SVGIcon from '@/components/SVGIcon';
import List from '@/components/List';
import Input from '@/components/Input';

export default {
  components: {
    Button,
    ButtonCircle,
    SVGIcon,
    Input,
    List
  },
  data() {
    return {
      input: {
        title: "",
        text: "",
      },
      validation: '',
      isUpdate: false,
      updateTextId: null,
      showOption: null,
      actionCompleteId: null,
    }
  },
  computed: {
    userRef(){
      return this.$store.getters.userRef
    },
    lists(){
      return this.$store.state.text.all
    }
  },
  mounted(){
    this.$store.dispatch('text/List', null, {root: true})
  },
  methods: {
    isUrl(string){
      var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
        '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
      return !!pattern.test(string);
    },
    toggle(id){
      if (!this.isUpdate || this.showOption == id) {
        if(this.showOption == id) {
          this.showOption = null
          this.input = {
          title: '',
          text: ''
        } 
        this.isUpdate = false
        } else this.showOption = id
      }
    },
    copyText({id, content}) {
      this.$copyText(content).then(() => {
        this.actionCompleteId = id
        setTimeout(() => this.actionCompleteId = null, 1000)
      }).catch(() => alert('not copied'))
    },
    editText({title,content, id}) {
      window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' })
      this.isUpdate = true
      this.input.title = title
      this.input.text = content
      this.updateTextId = id
    },
    saveText() {
      if (this.input.title && this.input.text) {
        if (this.isUpdate) {
          const updateText = {
            updateTextId: this.updateTextId,
            title: this.input.title,
            text: this.input.text
          }
          this.$store.dispatch('text/Update', updateText, {root: true})
          this.isUpdate = false
        } else {
          const newText = {
            title: this.input.title,
            content: this.input.text,
            date: new Date().getTime(),
          }
          this.$store.dispatch('text/Save', newText, {root: true})
        }
        this.input.title = ""
        this.input.text = ""
        this.showOption = null
      } else {
        this.validation = "kok kosong? isi dong"
        setTimeout(() => {
          this.validation = ""
        }, 3000)
      }
    },
    deleteText(id) {
      if (confirm("yakin ?")) {
        this.$store.dispatch('text/Delete', id, {root: true})
        this.isUpdate = false
        this.input.title = ""
        this.input.text = ""
      }
    }
  },
}
</script>

<style scoped lang="postcss">
.stext-main {
  @apply bg-gradient-to-br from-gray-100 to-gray-300 flex flex-col items-center
}
.stext-title {
  @apply my-2 font-bold text-blue-500
}
.text-list {
  @apply py-2 px-5 text-base tracking-wide text-left w-full 
}
</style>