<template>
  <button 
    class="btn-circle mb-1" 
    :class="mode"
    @click="$emit('klik')">
    <slot></slot>
  </button>
</template>

<script>
export default {
  props: {
    mode: {
      type: String,
      required: true,
      validator: (value) => {
        return ['success', 'warning', 'danger'].indexOf(value) !== -1
      }
    }
  }
}
</script>

<style lang="postcss" scoped>
  .btn-circle{
    @apply flex justify-center items-center text-white rounded-full p-2 mr-1 h-8 w-8 mx-1;
  }
  .btn-circle:focus{
    @apply outline-none;
  }

  .success{
    @apply bg-green-400; 
  }
  .success:hover{
    @apply bg-green-500;
  }

  .warning{
    @apply bg-orange-400; 
  }
  .warning:hover{
    @apply bg-orange-500;
  }

  .danger{
    @apply bg-red-500;
  }
  .danger:hover{
    @apply bg-red-600;
  }
</style>